<template>
  <div>
    <div class="mainViewCell">
      <div class="mainViewCell-leftBox">
        <!-- ------------------------------- -->
        <div :class="isCollapse?'MenuBtn transform-arrow':'MenuBtn'" @click="MenuBtnClick"></div>
        <el-menu
          :default-active="firstPage"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="selectFun"
          :collapse="isCollapse"
          style="background-color:RGB(24,79,168)"
        >
          <template v-for="item in MenuArr">
            <router-link :to="{ path: item.url, query: { menuCode: item.menuCode}}" v-if="item.arr.length == 0" :key="item.menuCode">
              <el-menu-item :index="item.menuCode" style="text-align: left;">
                <i :class="item.iName" style="color:white"></i>
                <span slot="title">{{item.name}}</span>
              </el-menu-item>
            </router-link>
            <el-submenu
              v-else
              :index="item.menuCode"
              :key="item.menuCode"
              style="text-align: left;"
            >
              <template slot="title">
                <i :class="item.iName" style="color:white"></i>
                <span slot="title" style="color:RGB(124,176,253);">{{item.name}}</span>
              </template>
              <!-- --------- -->
              <template v-for="item2 in item.arr">
                <router-link :to="{ path: item2.url, query: { menuCode: item2.menuCode}}" v-if="item2.arr.length == 0" :key="item2.menuCode">
                  <el-menu-item
                    :index="item2.menuCode"
                    style="text-align: left;padding-left:56px"
                  >{{item2.name}}</el-menu-item>
                </router-link>
                <el-submenu
                  v-else
                  :index="item2.menuCode"
                  :key="item2.menuCode"
                >
                  <span slot="title" style="margin-left:13px;color:RGB(124,176,253);">{{item2.name}}</span>
                  <template v-for="item3 in item2.arr">
                    <router-link :to="{ path: item3.url, query: { menuCode: item3.menuCode}}" :key="item3.menuCode">
                      <el-menu-item
                        :index="item3.menuCode"
                        style="text-align: left;padding-left:56px"
                      >{{item3.name}}</el-menu-item>
                    </router-link>
                  </template>
                </el-submenu>
              </template>
              <!-- --------- -->
            </el-submenu>
          </template>
        </el-menu>
        <!-- ---------------------------------->
      </div>
      <!-- =========================第二视口 -->
      <div class="mainViewCell-rightBox">
        <router-view name="SecondView"></router-view>
      </div>
      <!-- ========================= 第二视口-->
    </div>
  </div>
</template>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;

  background-color: RGB(28, 99, 200);
}
.el-menu {
  border-right: none;
}
.el-menu-item,
.el-submenu {
  background-color: RGB(28, 99, 200);
}
.el-menu-item,
.el-submenu__title {
  color: RGB(124, 176, 253);
}

.el-menu-item.is-active {
  color: RGB(234, 251, 6);
}
/* ========== */
.el-menu-item:hover,
.el-menu-item:focus {
  background-color: #1c63c8 !important;
}

/* =========== */
</style>

<script>
export default {
  created() {
  },
  watch: {
    "$store.state.leftMenuBox": {
      handler(newVal) {
        this.firstPage = newVal;
        this.storage.set("leftMenuBox", newVal);
      }
    },
    "$store.state.leftMenuBoxBody": {
      handler(newVal) {
        this.MenuArr = newVal;
      },
      deep: true
    }
  },
  data() {
    return {
      firstPage: this.$store.state.leftMenuBox,
      isCollapse: false,
      // ======================后台模板不要删除
      // MenuArr: [
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/processManage", name: "流程管理" },
      //       {
      //         arr: [],
      //         iName: "",
      //         url: "/operateTaskManage",
      //         name: "运行任务管理"
      //       },
      //       {
      //         arr: [],
      //         iName: "",
      //         url: "/operateTaskMonitor",
      //         name: "运行任务监控"
      //       },
      //       { arr: [], iName: "", url: "/operateTaskLog", name: "运行任务日志" }
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "数据集成"
      //   },
      //   {
      //     arr2: [
      //     ],
      //     to: "/cacheManage",
      //     iName: "el-icon-location",
      //     name: "缓存管理"
      //   },
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/userManagement", name: "用户管理" },
      //       { arr: [], iName: "", url: "/roleManagement", name: "角色管理" },
      //       { arr: [], iName: "", url: "/organization", name: "组织机构" }
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "用户权限"
      //   },
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/countryRegion", name: "国家地区" },
      //       { arr: [], iName: "", url: "/adminDiv", name: "行政区划" },
      //       { arr: [], iName: "", url: "/currencyCode", name: "货币代码" },
      //       { arr: [], iName: "", url: "/verCode", name: "通用代码" }
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "基础设置"
      //   },
      //   {
      //     arr: [],
      //     url: "/paramSet",
      //     iName: "el-icon-location",
      //     name: "参数设置"
      //   },
      //   {
      //     arr: [],
      //     url: "/optionLog",
      //     iName: "el-icon-location",
      //     name: "操作日志"
      //   }
      // ]
       MenuArr:  this.$store.state.leftMenuBoxBody
    };
  },
  methods: {
    handleOpen(key, keyPath) {
    },
    handleClose(key, keyPath) {
    },
    selectFun(a) {
      this.$store.commit("leftMenuBox", {
        leftMenuBox: a
      });
    },
    MenuBtnClick() {
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>


